.card {
  background: #fff;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 100%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.navbar{
    background: #5133a4;
    min-height: 70px;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #fff;
    font-size: 1rem;
    /* font-size: calc(10px + 14 * (100vw - 320px)/ 800); */
    z-index: 99;
    white-space: nowrap;
}
.navbar ul {
  margin: 0;
  padding: 0;
  width: 100%;
}
.navbar li {
  display: inline-block;
  padding: 15px 0;
  width: 33.3%;
  text-align: center;
}
.header {
    background-color: #336600;
    /* background-image: url(/4de356f…/00972/assets/images/geometry.svg); */
    background-repeat: repeat;
    color: #fff;
    min-height: 150px;
    padding: 125px 20px 75px 20px;
    /* padding-bottom: 75px; */
    text-align: center;
}
.wider{
    width: 100%;
    margin: 0;
    padding: 0;
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

